header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 15vh;
    background-color: #FFC107;
    box-shadow: 1px 1px 5px grey;
}
header h1{
    margin-left: 30px;
    color: #fffdf6;
    font-size: 3rem;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    text-shadow: 1px 1px 3px rgb(56, 53, 48);
}

.noteIcon{
    margin-right: 100px;
    cursor: pointer;
}

ul{
    display: flex;
    flex-flow: row wrap ;
}

ul li{
    list-style: none;
}